import { Component } from "react";
import { Link } from "react-router-dom";
import axios from 'axios'
import { FaRegTrashAlt, FaFileSignature} from 'react-icons/fa'


 
class ViewProduct extends Component {
    state =
        {
            items: []
        }
 
    //load initially with the load of webpage
    componentDidMount() {
        axios.get('http://localhost:90/product/all')
            .then((alldata) => {
                console.log(alldata)
                this.setState({
                    items: alldata.data.productData
                })
            })
            .catch((err) => {
                console.log(err.response)
            })
    }
 
    deleteproduct = (id) => {
        axios.delete("http://localhost:90/product/delete/" + id)
            .then((response) => {
                console.log(response)
                alert(response.data.message)
                window.location.reload()
                
            })
            .catch((error) => {
                console.log(error.response)
            })
    }
 
    render() {
        if (localStorage.getItem('token') && localStorage.getItem('userType') === 'Admin') {

            var viewproduct =
            <div className="container-fluid">
            <div className="row">
                {
                    this.state.items.map((item) => {
                        return (
                           
                            <div className="card shadow-lg" style={{width: '18rem', height:'auto', marginLeft:'14px', marginTop:'10px', marginBottom:'10px'}}>
                                <img className="card-img-top" src={"http://localhost:90/" + item.productImage.replace("\\", "/")} alt="Image Loading...." style={{ width: 'auto', height: '150px' }} />
                                <div className="card-body">
                                    <h5 className="card-title" style={{color:'red'}}> {item.productName}</h5><br />
                                    <label><b>NPR</b> </label> {item.productRate}<br/>
                                    <label><b>Description:</b> </label> {item.productDescription.split("T")[0]}<br/>
                                    <p><label><b>Company:</b> </label> {item.productCompany.split("T")[0]} <br/><label><b>Category:</b> </label> {item.productCategory}</p>
                                    <Link to={'/updateproduct/' + item._id}><button type="button" className="btn btn-primary btn-sm"><FaFileSignature/></button></Link>  <button type="button" className="btn btn-danger btn-sm" onClick={this.deleteproduct.bind(this, item._id)}><FaRegTrashAlt/></button>
                                </div>
                            </div>
                           
                        )
                    })
                }
            </div>
        </div>
        

        }

        return (
            <div>
                {viewproduct}
            </div>
           
        )
    }
}
 
export default ViewProduct;