import { Component } from "react";
import axios from 'axios'
class UpdateProduct extends Component {
    state = {
        productName: "",
        productRate: "",
        productDescription: "",
        productCompany: "",
        productCategory: "",
        id: this.props.match.params.id     
    }

    changeDetails = (e) => {
        this.setState({
            [e.target.name]: e.target.value
        })
    }



    componentDidMount() {  
        
        axios.get("http://localhost:90/product/single/" + this.state.id)
            .then((response) => {
                
                console.log(response.data)
                this.setState({
                    productName: response.data.data.productName,
                    productRate: response.data.data.productRate,
                    productDescription: response.data.data.productDescription,
                    productCompany: response.data.data.productCompany,
                    productCategory: response.data.data.productCategory
                    
                })
            })
            .catch((err) => {
                console.log(err.response)
            })
    }

    UpdateProduct = (e) => {    
        e.preventDefault();
        axios.put('http://localhost:90/product/update/' + this.state.id, this.state)
            .then((response) => {
                console.log(response)
               alert(response.data.message)  
               window.location.reload()             
            })
            .catch((err) => {
                console.log(err.response)
            })
    }

    render() {
        return (
            <div className="wrapper">
                <h1>Update Product</h1>
                <form>
                    <fieldset>
                        <label>
                            <p>Product Name
                         <input type="text" name="productName" value={this.state.productName}
                                    onChange={this.changeDetails} />
                            </p>
                        </label>
                    </fieldset>

                    <fieldset>
                        <label>
                            <p>Product Rate
                         <input type="text" name="productRate" value={this.state.productRate}
                                    onChange={this.changeDetails} />
                            </p>
                        </label>
                    </fieldset>

                    <fieldset>
                        <label>
                            <p>Product Description
                         <input type="text" name="productDescription" value={this.state.productDescription}
                                    onChange={this.changeDetails} />
                            </p>
                        </label>
                    </fieldset>



                    <fieldset>
                        <label>
                            <p>Product Company
                         <input type="text" name="productCompany" value={this.state.productCompany}
                                    onChange={this.changeDetails} />
                            </p>
                        </label>
                    </fieldset>

                    <fieldset>
                        <label>

                            <p>Product Category
                         <input type="text" name="productCategory" value={this.state.productCategory}
                                    onChange={this.changeDetails} />
                            </p>
                        </label>
                    </fieldset>

                  

                    <p><button type="submit" name="UpdateProduct" className="btn btn-primary" onClick={this.UpdateProduct}>Update Product</button>
                    </p>
                </form>
            </div>

        )
    }
}
export default UpdateProduct;